// src/App.js
import React, { Component } from 'react';
import { db } from './firebase';
import Form from './Form';
import Calendar from './Calendar';
import './App.css';

class App extends Component {
  constructor(props) {
    super(props);

    this.state = {
      uid: window.firebaseUID || '', // UID injected from Kotlin
      crops: [],
      loading: true,
    };
  }

  componentDidMount() {
  // Listen for UID from Kotlin
  window.addEventListener('firebase-uid-ready', () => {
    const uid = window.firebaseUID;
    if (uid) {
      this.setState({ uid, loading: true }, () => {
        this.fetchCrops(() => {
          alert("Login successful! You can now add crops.");
        });
      });
    }
  });

  // If UID already exists (rare, but possible)
  if (window.firebaseUID) {
    this.setState({ uid: window.firebaseUID, loading: true }, () => {
      this.fetchCrops(() => {
        alert("Login successful! You can now add crops.");
      });
    });
  } else {
    this.setState({ loading: false });
  }
}

// Modify fetchCrops to accept a callback
fetchCrops = (callback) => {
  const { uid } = this.state;

  db.collection("crops")
    .where("userId", "==", uid)
    .get()
    .then(snapshot => {
      const cropsList = snapshot.docs.map(doc => {
        const data = doc.data();
        if (!data.seasons || data.seasons.length === 0) {
          const year = new Date().getFullYear();
          data.seasons = [{ start: `${year}-01-01`, end: `${year}-03-31` }];
        }
        return { id: doc.id, ...data };
      });
      this.setState({ crops: cropsList, loading: false }, callback);
    })
    .catch(error => {
      console.error("Error fetching crops:", error);
      this.setState({ loading: false }, callback);
    });
};


  addCrop = () => {
    const { uid, crops } = this.state;
    const year = new Date().getFullYear();
    const newCrop = {
      name: "",
      userId: uid,
      seasons: [],
    };

    // Auto-schedule 4 batches by default
    const batches = 4;
    const batchMonths = Math.floor(12 / batches);
    for (let i = 0; i < batches; i++) {
      const startMonth = i * batchMonths;
      const endMonth = startMonth + batchMonths - 1;
      const startDate = new Date(year, startMonth, 1).toISOString().split('T')[0];
      const endDate = new Date(year, endMonth + 1, 0).toISOString().split('T')[0];
      newCrop.seasons.push({ start: startDate, end: endDate });
    }

    db.collection("crops")
      .add(newCrop)
      .then(docRef => {
        this.setState({ crops: [...crops, { ...newCrop, id: docRef.id }] });
      })
      .catch(console.error);
  };

  deleteCrop = index => {
    const { crops } = this.state;
    const crop = crops[index];
    if (crop.id) db.collection("crops").doc(crop.id).delete().catch(console.error);
    this.setState({ crops: crops.filter((_, i) => i !== index) });
  };

  updateCrop = (index, updated) => {
    const crops = [...this.state.crops];
    crops[index] = { ...crops[index], ...updated };
    this.setState({ crops });
    if (crops[index].id) db.collection("crops").doc(crops[index].id).update(crops[index]).catch(console.error);
  };

  addSeasonToCrop = index => {
    const crops = [...this.state.crops];
    crops[index].seasons.push({ start: "", end: "" });
    this.setState({ crops });
  };

  saveAllCrops = () => {
    this.state.crops.forEach(crop => {
      if (crop.id) db.collection("crops").doc(crop.id).update(crop).catch(console.error);
      else db.collection("crops").add(crop).catch(console.error);
    });
    alert("All crops saved!");
  };

  render() {
    const { loading, crops } = this.state;

    if (loading) return <p>Loading crops...</p>;

    return (
      <div id="app">
        <div className="app-bar">
          <h1>Harvest Calendar</h1>
        </div>

        <header>
          <p>Plan pineapple farming with equal harvest distribution.</p>
        </header>

        <div id="form-container">
          <div id="instructions">
            <h3>How to use</h3>
            <p>Add crops and schedule the seasons.</p>
            <button className="btn btn-secondary" onClick={this.addCrop}>Add Crop</button>
            <button className="btn btn-primary" onClick={this.saveAllCrops}>Save</button>
          </div>

          <Form
            crops={crops}
            updateCrop={this.updateCrop}
            deleteCrop={this.deleteCrop}
            addSeasonToCrop={this.addSeasonToCrop}
          />
        </div>

        <Calendar crops={crops} />
      </div>
    );
  }
}

export default App;



